export default {
	data() {
		return {
			goods: [],
			page: 1,
			stopLoad: false,
			type: null
		}
	},
	onLoad(options) {
		console.log(options)
		this.type = options.type
		this.getGoodsList()
	},
	methods: {
		toDetail(goods_id){
			uni.redirectTo({
				url: "../goodsDetail/goodsDetail?id=" + goods_id
			})
		},
		toSearch(){
			uni.navigateTo({
				url:"../search/search"
			})
		},
		//获取商品列表
		getGoodsList() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsmore",
				data: {
					shop_id: uni.getStorageSync('shop_id')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if(this.type=="is_new"){
							this.goods = res.data.data.new_goods
						}else if(this.type=="is_hot"){
							this.goods = res.data.data.hot_goods
						}else if(this.type=="is_recommend"){
							this.goods = res.data.data.recommend_goods
						}
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//加载更多商品
		loadMoreGoods() {
			if (this.stopLoad) {
				return
			}
			this.page++
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsList",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					page: this.page,
					offset: 6,
					field: this.type,
					shop_id: uni.getStorageSync('shop_id')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.goods.length == 0) {
							this.stopLoad = true
							return
						}
						for (var i = 0; i < res.data.data.goods.length; i++) {
							this.goods.push(res.data.data.goods[i])
						}
					}
				}
			})
		}
	},
	//页面触底加载更多精选商品
	// onReachBottom() {
	// 	this.loadMoreGoods()
	// }
}
